.projects-container {
  background-color: black;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10vh;
}

.projects-container>h1 {
  font-family: "Rasa", serif;
  font-style: italic;
  font-size: 3vw;
  letter-spacing: 0.5rem;
}

.projects-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.titles {
  white-space: nowrap;
  display: flex;
  flex-direction: column;
  font-size: 5vh;
}

.titles h1 {
  letter-spacing: 0.3rem;
  z-index: 2;
  opacity: 0.5;
}
.titles h1.active {
  opacity: 1;
}

.display-wrapper {
  margin-left: -12VW;
  width: 100%;
}

.display {
  position: relative;
  height: 60vh;
  width: 60vh;
  border-radius: 1rem;
}

.display video {
  opacity: 1;
  filter: brightness(0.7) grayscale(0.3);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  object-fit: cover;
  border-radius: 1rem;
  z-index: 1;
}

.button-wrapper {
  position: absolute;
  left: 50%;
  bottom: 1rem;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  z-index: 3;
}

.button-wrapper button {
  white-space: nowrap;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: none;
  width: 25vh;
  font-size: 2vh;
  border-radius: 1rem;
  padding:1vh 5vh;
}

.button-wrapper button img {
  width: 1rem;
}

.button-wrapper .website {
  background-color: white;
  color: black;
  transition: all 0.4s ease-in-out;
}

.button-wrapper .website img {
  filter: invert(1);
}

.button-wrapper .website:hover {
  background-color: black;
  color: white;
}

.button-wrapper .website:hover img {
  filter: none;
}

.button-wrapper .case-study {
  background: black;
  color: white;
  transition: all 0.4s ease-in-out;
}

.button-wrapper .case-study:hover {
  background-color: white;
  color: black;
}

.button-wrapper .case-study:hover img {
  filter: invert(1);
}

/* Mobile devices (portrait and landscape) */
@media only screen and (max-width: 767px) {
  .display {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 30vh;
    width: 30vh;
    border-radius: 1rem;
  }

  .display-wrapper {
    margin: 0;
  }

  .projects-wrapper {
    flex-direction: column;
    gap: 4vh;
  }

  .titles {
    width: 100%;
    font-size: 1.4vh;
  }

  .button-wrapper button {
    width: 13vh;
    font-size: 1.5vh;
    border-radius: 2rem;
    padding:0.75vh 1.75vh;
  } 
}

/* Tablet devices (portrait and landscape) */
@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .display {
    position: relative;
    height: 50vh;
    width: 50vh;
    border-radius: 1rem;
  }

  .titles {
    font-size: 3vh;
  }

  .button-wrapper button {
    width: 22vh;
    border-radius: 2rem;
  } 
}

/* Laptop devices (portrait and landscape) */
@media only screen and (min-width: 1024px) and (max-width: 1440px) {
  .display {
    position: relative;
    height: 55vh;
    width: 55vh;
    border-radius: 1rem;
  }

  .titles {
    font-size: 4vh;
  }
}

